import { Contract } from 'ethers';
import getAddress from './getGameAddress';
import abi from '../abi/game.json';



export default function getGameContract(chainNumber, library, withSigner) {
  const address = getAddress(chainNumber);
  if (!address) return null;
  if (withSigner)
    return new Contract(address, abi, library.getSigner());
  return new Contract(address, abi, library);
}
