const WalletAccountModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) document.body.classList.add("no-scroll");
    else document.body.classList.remove("no-scroll");
  }, [isOpen]);

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const { logout } = useAuth();
  const connectorLocalStorageKey = 'connectorId';
  const handleLogout = () => {
    logout();
    window.localStorage.removeItem(connectorLocalStorageKey);
    setOpen(false);
  }


  const etherscan = `https://${chainId === 5 ? 'goerli.' : ''}etherscan.io`;
  const accountShort = account
    ? `${account.substring(0, 6)}...${account.substring(account.length - 5)}`
    : 'Connect wallet';



  return (
    <Modal
      id={'WalletAccountModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
      width={300}
      height={300}
      radius={10}
      backgroundColor='#212529'
    >
      <div
        style={{ height: '100%' }}
      >
        <Header as='h1' inverted>{accountShort}</Header>
        <br />
        <div>
        </div>
        <Link to='/game'>
          <CommonButton onClick={() => setOpen(false)}
            style={{ width: '100%', marginBottom: '10px' }}
          >Open Game</CommonButton>
        </Link>
      </div>
    </Modal>
  );
};

export default hot(module)(WalletAccountModal);
