import { hot } from 'react-hot-loader';
import './IconWrapper.scoped.scss';



function IconWrapper({ className, children, width = 24, height = 24, color = 'currentColor', }) {
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      width={width}
      height={height}
      style={{ minWidth: `{width}px`, minHeight: `{height}px` }}
      viewBox={`0 0 ${width} ${height}`}
      role="presentation"
      //v-bind="$attrs"
      className={`${className ? className : ''}`}
    >
      <g fill={color}>
        {children}
      </g>
    </svg>
  );
}

export default hot(module)(IconWrapper);
