import { useState } from 'react';
import { hot } from 'react-hot-loader';
import { useTranslation } from 'react-i18next';
import './ElementLang.scoped.scss';




function ElementLang({ className }) {
  const [state, setState] = useState(false);
  const { i18n, t } = useTranslation();
  const onClick = (code) => {
    i18n.changeLanguage(code);
    setState(false);
  }
  return (
    <div className={`lang ${state ? 'active' : ''} ${className ? className : ''}`}>
      <div className="lang__header" onClick={() => setState(!state)}>
        <div className="lang__txt">Language: {i18n.language.toUpperCase()}</div>
        <img src="/assets/images/chevron-d.svg" alt="" title="" width="24" height="24" className="lang__icon" />
      </div>

      {state && <div className="lang__drop">
        <div className="lang__lang" onClick={() => onClick('en')}>English</div>
        {/*<div className="lang__lang" onClick={() => onClick('ru')}></div>*/}
      </div>}
    </div>
  );
}

export default hot(module)(ElementLang);
