import { useState, useEffect, useCallback, useReducer } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { hot } from 'react-hot-loader';
import { DefaultLayout } from '../layouts';
import './TermsPage.scoped.scss';
import './GamePage.scss';


import { toast } from 'react-toastify';
import Number from 'react-number-format';
import { useModal } from 'react-simple-modal-provider';
import { useEagerConnect, useActiveWeb3React } from '../hooks/index';
import useAuth from '../hooks/useAuth';
import copy from 'copy-to-clipboard';

import { Button, Header, Icon, Segment, Tab, Grid, Statistic, List, Image, Radio } from 'semantic-ui-react';
import { CommonButton } from '../components';

import { utils } from 'ethers';
import * as A from '../store/actions';
import * as S from '../store/selectors';

import getNftContract from '../hooks/getNftContract';
import getGameContract from '../hooks/getGameContract';
import getGameAddress from '../hooks/getGameAddress';


import { formatSI } from 'format-si-prefix';
//const string = formatSI(number);

const LOOT_TO_HIRE_1MOBSTER = 8640000;




function exampleReducer(state, action) {
  const list = JSON.parse(JSON.stringify(state));
  switch (action.type) {
    case 'ADD_REMOOVE':
      if (state.includes(action.payload)) {
        list.splice(list.indexOf(action.payload), 1);
        return list;
      } else {
        return list.concat([action.payload]);
      }

      return [];
    default:
      throw new Error();
  }
}

const panes = [
  { menuItem: 'GAME', render: () => <Tab.Pane>Tab 1 Content</Tab.Pane> },
  { menuItem: 'REF', render: () => <Tab.Pane>Tab 3 Content</Tab.Pane> },
  { menuItem: 'NFT', render: () => <Tab.Pane>Tab 2 Content</Tab.Pane> },
]
const TabExampleBasic = () => <Tab menu={{ attached: false }} panes={panes} />



function GamePage() {
  const dispatch = useDispatch();


  const [timeNow, setTimeNow] = useState(0);

  const [cardsAll, setCardsAll] = useState([]);
  useEffect(() => {
    fetch('/cards/all.json').then(res => res.json()).then(json => setCardsAll(json)).catch(err => null);

    setTimeNow(Math.trunc(new Date().getTime() / 1000))
    const intervalId = setInterval(() => {
      setTimeNow(Math.trunc(new Date().getTime() / 1000))
    }, 1000);
    return () => {
      clearInterval(intervalId);
    };
  }, []);

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const { open: openWalletConnectModal } = useModal('WalletConnectModal');
  const accountShort = 'Connect wallet';
  const connectWallet = (e) => {
    e.preventDefault();
    openWalletConnectModal();
  }


  const parentAddress = useSelector(S.ref.getParentAddress);

  const blockNumber = useSelector(S.provider.getBlockNumber);
  const [isUpdating, setUpdating] = useState(false);

  const [balanceEthGame, setBalanceEthGame] = useState(0.00);
  const [initializedAt, setInitializedAt] = useState(0);
  const [maxLimit, setMaxLimit] = useState(0);

  const [balanceEthUser, setBalanceEthUser] = useState(0.00);
  const [userRewardsEth, setRewardsEth] = useState(0.00);
  const [userExtraRate, setUserExtraRate] = useState(0);
  const [userData, setUserData] = useState({
    lastClaim: 0,
    loot: '0',
    mobsters: '0',
    totalDeposit: 0.0,
    totalReinvest: 0.0,
    totalRefIncome: 0.0,
    totalRefs: 0,
    user: '0x...',
  });


  useEffect(() => {
    if (initializedAt === 0) {
      setMaxLimit(0.0);
    } else {
      const step = Math.floor((
        Math.floor(Date.now() / 1000) - initializedAt
      ) / (7 * 24 * 60 * 60)) + 1;
      const DEPOSIT_STEP = 2;// 2 eth
      setMaxLimit(parseFloat(
        (step * DEPOSIT_STEP - (userData.totalDeposit + userData.totalReinvest)).toFixed(4)
      ));
    }

  }, [initializedAt, userData, blockNumber]);

  const [userTokens, setUserTokens] = useState([]);
  const [userTokensStaked, setUserTokensStaked] = useState([]);
  const [userTokensUnstaked, setUserTokensUnstaked] = useState([]);


  const [buyLootValue, setBuyLootValue] = useState(0);
  const [newMobsters, setNewMobsters] = useState(0);
  const onChangeBuyLootValue = async (e) => {
    e.preventDefault();//maxLimit
    let valueEth = parseFloat(e.target.value);
    if (valueEth > maxLimit) valueEth = maxLimit;
    setBuyLootValue(valueEth);
    try {
      const constractGame = getGameContract(chainId, library);
      if (constractGame) {
        const value = utils.parseEther((valueEth).toString());
        const res = await constractGame.calculateLootBuy(value);
        setNewMobsters(res.toNumber());
      }
    } catch (error) {
      console.error(error);
      //error.message
    }
  }

  const onBlockHandler = async () => {
    if (isUpdating) return;
    setUpdating(true);

    dispatch(A.provider.setBlockNumber(blockNumber));
    try {
      let balanceEthGame = await library.getBalance(getGameAddress(chainId));
      balanceEthGame = utils.formatEther(balanceEthGame.toString());
      balanceEthGame = parseFloat(parseFloat(balanceEthGame).toFixed(4));
      setBalanceEthGame(balanceEthGame);


      let balanceEthUser = await library.getBalance(account);
      balanceEthUser = utils.formatEther(balanceEthUser.toString());
      balanceEthUser = parseFloat(parseFloat(balanceEthUser).toFixed(9));
      setBalanceEthUser(balanceEthUser);

      const constractGame = getGameContract(chainId, library);
      if (constractGame) {
        const initializedAt = await constractGame.initializedAt();
        setInitializedAt(initializedAt.toNumber());

        const userDataOrig = await constractGame.users(account);
        let userData = {
          lastClaim: userDataOrig.lastClaim.toNumber(),
          loot: userDataOrig.loot.toString(),
          mobsters: userDataOrig.mobsters.toString(),
          totalDeposit: userDataOrig.totalDeposit.toString(),
          totalReinvest: userDataOrig.totalReinvest.toString(),
          totalRefIncome: userDataOrig.totalRefIncome.toString(),
          totalRefs: userDataOrig.totalRefs.toNumber(),
          user: userDataOrig.user,
        };
        userData.totalDeposit = utils.formatEther(userData.totalDeposit);
        userData.totalDeposit = parseFloat(parseFloat(userData.totalDeposit).toFixed(9));

        userData.totalReinvest = utils.formatEther(userData.totalReinvest);
        userData.totalReinvest = parseFloat(parseFloat(userData.totalReinvest).toFixed(9));

        userData.totalRefIncome = utils.formatEther(userData.totalRefIncome);
        userData.totalRefIncome = parseFloat(parseFloat(userData.totalRefIncome).toFixed(9));

        const loot = await constractGame.getMyLoot(account);
        userData.loot = loot.toString();
        setUserData(userData);

        try {
          let userRewardsEth = await constractGame.getMyRewards(account);
          userRewardsEth = utils.formatEther(userRewardsEth.toString());
          userRewardsEth = parseFloat(parseFloat(userRewardsEth).toFixed(9));
          setRewardsEth(userRewardsEth);
        } catch (error) { }
      };

      const constractNft = getNftContract(chainId, library);
      if (constractNft) {
        const userExtraRate = await constractNft.extraRate(account);
        setUserExtraRate(userExtraRate.toNumber());

        const userTokensOrig = await constractNft.tokensOfOwner(account);
        const userTokens = [];
        for (let i in userTokensOrig) {
          userTokens.push(userTokensOrig[i].toNumber());
        }
        setUserTokens(userTokens);

        const userTokensStakedOrig = await constractNft.tokensStakedOfOwner(account);
        const userTokensStaked = [];
        for (let i in userTokensStakedOrig) {
          userTokensStaked.push(userTokensStakedOrig[i].toNumber());
        }
        setUserTokensStaked(userTokensStaked);

        const userTokensUnstaked = [];
        for (let i in userTokens) {
          if (!userTokensStaked.includes(userTokens[i])) {
            userTokensUnstaked.push(userTokens[i]);
          }
        }
        setUserTokensUnstaked(userTokensUnstaked);
      }

    } catch (error) {
      console.error(error);
      //dispatch(A.nft.clear());
    }
    setUpdating(false);
  }

  useEffect(() => {
    onBlockHandler();
  }, [blockNumber]);


  const onBuy = async () => {
    try {
      const constractGame = getGameContract(chainId, library, true);
      if (constractGame) {
        const value = utils.parseEther((buyLootValue).toString());
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractGame.estimateGas.buyLoot(
          parentAddress,
          { value, gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractGame.buyLoot(
          parentAddress,
          { value, gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }
  const onHire = async () => {
    try {
      const constractGame = getGameContract(chainId, library, true);
      if (constractGame) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractGame.estimateGas.hireMobsters(
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractGame.hireMobsters(
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }
  const onSell = async () => {
    try {
      const constractGame = getGameContract(chainId, library, true);
      if (constractGame) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractGame.estimateGas.sellLoot(
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractGame.sellLoot(
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }

  const onStakeAll = async (list2 = userTokensUnstaked) => {
    const list = [];
    for (let i in list2) {
      if (userTokensUnstaked.includes(list2[i])) {
        list.push(list2[i]);
      }
    }
    try {
      const constractNft = getNftContract(chainId, library, true);
      if (constractNft) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;
        gasLimit = await constractNft.estimateGas.stake(
          list,
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractNft.stake(
          list,
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }

  const onUnstakeAll = async (list2 = userTokensStaked) => {
    const list = [];
    for (let i in list2) {
      if (userTokensStaked.includes(list2[i])) {
        list.push(list2[i]);
      }
    }
    try {
      const constractNft = getNftContract(chainId, library, true);
      if (constractNft) {
        let gasPrice = await library.getGasPrice();
        gasPrice = gasPrice.mul(120).div(100);
        let gasLimit;

        gasLimit = await constractNft.estimateGas.unstake(
          list,
          { gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);

        const tx = await constractNft.unstake(
          list,
          { gasPrice, gasLimit, }
        );
        toast('TX is Pending.');
        await tx.wait(1);
        toast('TX is Confirmed.');
      }
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }


  const onStakeSelected = async () => {
    try {
      onStakeAll(selectedIds);
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }
  const onUnstakeSelected = async () => {
    try {
      onUnstakeAll(selectedIds);
    } catch (error) {
      toast.error('Error. ' + error.message);
      console.error(error);
    }
  }

  //const [selectedIds, setSelectedIds] = useState([]);
  const [selectedIds, setSelectedIds] = useReducer(exampleReducer, []);
  const handleChangeSelected = useCallback((tokeId) => {
    setSelectedIds({ type: 'ADD_REMOOVE', payload: tokeId });
  }, [selectedIds]);


  if (!triedEager || !library || !active)
    return (
      <DefaultLayout title='GAME'>
        <main className="main" style={{ minHeight: '95vh', }}>
          <section className="section section-terms">
            <div className="container">
              <Segment
                textAlign='center'
                style={{
                  background: 'rgba(255, 255, 255, 0.2)',
                  borderRadius: '24px',
                  minHeight: '320px',
                }}
              >
                <br />
                <Header as='h1' inverted>Wallet not connected</Header>
                <br />
                <br />
                <div>
                  <CommonButton variant="mod" onClick={connectWallet}>{accountShort}</CommonButton>
                </div>
              </Segment>
            </div>
          </section>
        </main>
      </DefaultLayout>
    );


  const timeBefore = (userData.lastClaim + 7 * 24 * 60 * 60) <= Math.floor(Date.now() / 1000)
    ? 0
    : (userData.lastClaim + 7 * 24 * 60 * 60) - Math.floor(Date.now() / 1000);
  const timeBeforeD = Math.floor((timeBefore / (24 * 60 * 60)));
  const timeBeforeH = Math.floor((timeBefore % (24 * 60 * 60)) / (60 * 60));
  const timeBeforeM = Math.floor((timeBefore % (60 * 60)) / 60);
  const timeBeforeS = Math.floor(timeBefore % 60);

  const handleCopyRefLink = () => {
    copy(`https://cashrush.gg/?r=${account}`);
    toast.info('Copied!');
  }

  return (
    <DefaultLayout title='GAME'>
      <main className="main" style={{ minHeight: '95vh', }}>
        <section className="section section-terms">
          <div className="container">
            <Segment
              textAlign='center'
              style={{
                background: 'rgba(0, 0, 0, 0.4)',
                borderRadius: '24px',
                minHeight: '320px',
              }}
            >
              <Grid columns={3} stackable>
                <Grid.Row>
                  <Grid.Column>
                    <Statistic label='My Mobsters' value={formatSI(userData.mobsters)} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='My Loot' value={`${formatSI(userData.loot)} (${userRewardsEth} ETH)`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Contract balance' value={`${balanceEthGame} ETH`} inverted />
                  </Grid.Column>
                </Grid.Row>
              </Grid>
              <div style={{ fontSize: '14px', /*color: '#ff892f',*/ }}>
                BUY LOOT to Hire {formatSI(newMobsters ? newMobsters : 0)} Mobsters with <input
                  id="buyLootValue"
                  name="buyLootValue"
                  type="number"
                  min={0}
                  max={100000}
                  step={0.05}
                  value={buyLootValue}
                  onChange={onChangeBuyLootValue}
                  style={{ backgroundColor: 'white', color: 'black', textAlign: 'center', }}
                /> ETH <i style={{ color: 'red', }}> <Number value={maxLimit} thousandSeparator prefix='max: ' suffix=' ETH' displayType='text' /></i>
                <br />
                <CommonButton onClick={() => onBuy()} style={{ margin: '10px', }} >BUY LOOT</CommonButton>
                <CommonButton onClick={() => onHire()} style={{ margin: '10px', }} >REHIRE MOBSTERS</CommonButton>
                <CommonButton onClick={() => onSell()} style={{ margin: '10px', }} >SELL LOOT</CommonButton>
              </div>
              <br />
              <hr style={{ backgroundColor: 'red', height: '2px', }} />
              <br />

              <Grid columns={3} stackable>
                <Grid.Row>
                  <Grid.Column>
                    <Statistic label='Time for newxt Rehire/Sell' value={`${timeBeforeD}d ${timeBeforeH}h ${timeBeforeM}m ${timeBeforeS}s`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Deposited' value={`${userData.totalDeposit} ETH`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Reinvested' value={`${userData.totalReinvest} ETH`} inverted />
                  </Grid.Column>
                </Grid.Row>
              </Grid>
              <br />

              <Grid columns={3} stackable>
                <Grid.Row>
                  <Grid.Column>
                    <Statistic label='Total Ref rewards' value={`${userData.totalRefIncome} ETH`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <CommonButton onClick={() => handleCopyRefLink()} style={{ margin: '10px', }} >COPY REF LINK</CommonButton>
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Total Referrals' value={`${userData.totalRefs}`} inverted />
                  </Grid.Column>
                </Grid.Row>
              </Grid>
              <br />
              <hr style={{ backgroundColor: 'red', height: '2px', }} />
              <br />

              <Grid columns={3} stackable>
                <Grid.Row>
                  <Grid.Column>
                    <Statistic label='Balance NFT' value={`${userTokens.length} NFT`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Staked NFT' value={`${userTokensStaked.length} NFT`} inverted />
                  </Grid.Column>
                  <Grid.Column>
                    <Statistic label='Extra rate' value={`${userExtraRate / 100} %`} inverted />
                  </Grid.Column>
                </Grid.Row>
              </Grid>

              <div>
                <CommonButton onClick={() => onStakeAll()} style={{ margin: '10px', }}>STAKE ALL</CommonButton>
                <CommonButton onClick={() => onUnstakeAll()} style={{ margin: '10px', }}>UNSTAKE ALL</CommonButton>
              </div>
              <br />
            </Segment>
          </div>
        </section>

        {(userTokens.length > 0) && <section className="section">
          <div className="container">
            <Segment
              //textAlign='center'
              style={{
                background: 'rgba(0, 0, 0, 0.4)',
                borderRadius: '24px',
                minHeight: '320px',
                marginTop: '50px',
                marginBottom: '50px',
              }}
            >
              <MyList
                cardsAll={cardsAll}
                userTokens={userTokens}
                userTokensStaked={userTokensStaked}
                selectedIds={selectedIds}
                handleChangeSelected={handleChangeSelected}
              />
              <br />
              <div>
                <CommonButton onClick={() => onStakeSelected()} style={{ margin: '10px', }}>STAKE SELECTED</CommonButton>
                <CommonButton onClick={() => onUnstakeSelected()} style={{ margin: '10px', }}>UNSTAKE SELECTED</CommonButton>
              </div>
              <br />
            </Segment>
          </div>
        </section>}
      </main>
    </DefaultLayout>
  );
};

function MyList({ cardsAll, userTokens, userTokensStaked, selectedIds, handleChangeSelected }) {
  return (
    <List divided celled verticalAlign='middle' inverted size='big'>
      {userTokens.map((tokenId) => {
        const tokenIndex = tokenId - 1;
        const cardData = cardsAll[tokenIndex];
        const rarity = cardData.attributes[0].value;
        const name = cardData.attributes[1].value;
        let color = undefined;
        if (rarity === 'LEGENDARY') color = '#ffd41a';
        if (rarity === 'RARE') color = '#ff9e36';
        if (rarity === 'COMMON') color = undefined;

        return (<List.Item key={tokenId} style={{ color, }}>
          <Image src={`/cards/${cardData.card}.jpg`} height={60} />
          <List.Content>
            #{tokenId} {name} {userTokensStaked.includes(tokenId) && '(STAKED)'}
          </List.Content>
          <List.Content floated='right'>
            <Segment compact inverted>
              <Radio toggle
                //name='radioGroup'
                value={tokenId}
                checked={selectedIds.includes(tokenId)}
                onChange={() => handleChangeSelected(tokenId)}
              />
            </Segment>
          </List.Content>
        </List.Item>);
      })}
    </List>
  );
}

export default hot(module)(GamePage);
