import { useEffect } from 'react';
import { hot } from 'react-hot-loader';
import { Link } from 'react-router-dom';
import { useModal } from 'react-simple-modal-provider';
import { useTranslation } from 'react-i18next';
import { Events as ScrollEvents, } from 'react-scroll';

import { useSelector, useDispatch, } from "react-redux";
import * as S from "../../store/selectors";
import * as A from "../../store/actions";

import { useMediaPredicate } from 'react-media-hook';
import { mobileBreakpoint } from '../../helpers/Media';
import { LinkExternal, CommonButton } from '../';
import { whitepaperUrl, twitterUrl, telegramUrl, contractUrl, audited } from '../../configs';
import './AppHeader.scss';
import { routerPaths } from '../../router/helper';

import { useEagerConnect, useActiveWeb3React } from '../../hooks/index';
import useAuth from '../../hooks/useAuth';


function AppHeader() {
  const isMobile = useMediaPredicate(mobileBreakpoint);
  const { i18n, t } = useTranslation();
  const isMobileMenuOpen = useSelector(S.mobileMenu.isOpen);
  const dispatch = useDispatch();
  const setMobileMenuOpen = (value) => {
    dispatch(A.mobileMenu.set(value));
  }


  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { login, logout } = useAuth();
  const { open: openWalletConnectModal } = useModal('WalletConnectModal');
  const { open: openWalletAccountModal } = useModal('WalletAccountModal');
  const { open: openMintModal } = useModal('MintModal');

  const { chainId, account, active } = context;
  const accountShort = account
    ? `MINT`// GAME
    : 'Connect wallet';

  const connectWallet = (e) => {
    e.preventDefault();
    setMobileMenuOpen(false);
    account
      ? openMintModal()
      : openWalletConnectModal();
  }

  useEffect(() => {
    ScrollEvents.scrollEvent.register('begin', function (to, element) {
      setMobileMenuOpen(false);
    });
    return () => {
      ScrollEvents.scrollEvent.remove('begin');
    };
  }, []);


  return (
    <header className="header" data-aos="fade-down">
      <div className="container relativefix">
        <div className="row align-items-center justify-content-between">
          <div className="col-auto order-lg-1">
            <Link to={routerPaths.main}>
              <img
                className="logo"
                src="/assets/images/logo.png"
                srcSet="/assets/images/logo@2x.png 2x"
                alt="" title=""
                width="" height=""
              />
            </Link>
          </div>
          <div className="col order-lg-2 d-none">
            <div className={`audited ${audited ? 'active' : ''}`}>Audited by GeorgeStamp</div>
          </div>
          <div className="col-auto order-lg-3 d-none d-lg-inline">
            <LinkExternal href={whitepaperUrl}><CommonButton>Whitepaper</CommonButton></LinkExternal>
          </div>
          <div className="col-auto order-lg-4 d-none d-lg-inline">
            <div className="soc">
              {/*<LinkExternal href={contractUrl}>
                <img src="/assets/images/document.svg" alt="" title="" width="" height="" />
                <span>Contract</span>
              </LinkExternal>*/}
              <LinkExternal href={twitterUrl}>
                <img src="/assets/images/twitter.svg" alt="" title="" width="" height="" />
                <span>Twitter</span>
              </LinkExternal>
              <LinkExternal href={telegramUrl}>
                <img src="/assets/images/telegram.svg" alt="" title="" width="" height="" />
                <span>Telegram</span>
              </LinkExternal>
            </div>
          </div>
          <div className="col-auto order-lg-5 d-none d-lg-inline">
            <CommonButton variant="mod" onClick={connectWallet}>{accountShort}</CommonButton>
          </div>
          <div className="col-auto order-lg-5 d-lg-none">
            <div className={`burger ${isMobileMenuOpen ? 'active' : ''}`}
              onClick={() => setMobileMenuOpen(!isMobileMenuOpen)}
            >
              {!isMobileMenuOpen && <svg
                className="burger__icon"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg">
                <g fill="none" fillRule="evenodd">
                  <path d="M0 0h24v24H0z" />
                  <path
                    d="M4.695 9.563h14.602a.707.707 0 1 0 0-1.414H4.695a.7.7 0 0 0-.703.703c0 .382.313.71.703.71zm0 3.515h14.602c.39 0 .71-.32.71-.703a.715.715 0 0 0-.71-.719H4.695c-.39 0-.703.32-.703.719 0 .383.313.703.703.703zm0 3.524h14.602c.39 0 .71-.32.71-.72 0-.39-.32-.702-.71-.702H4.695c-.39 0-.703.32-.703.703 0 .39.313.719.703.719z"
                    fill="#FFF"
                    fillRule="nonzero"
                  />
                </g>
              </svg>}
              {isMobileMenuOpen && <svg
                className="burger__icon"
                width="24"
                height="24"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg">
                <g fill="none" fillRule="evenodd">
                  <path d="M0 0h24v24H0z" />
                  <path
                    d="M5.957 18.406c.344.336.906.336 1.234.008l4.805-4.805 4.805 4.805a.882.882 0 0 0 1.234-.008.88.88 0 0 0 .008-1.226l-4.805-4.813 4.805-4.805a.872.872 0 0 0-.008-1.226.89.89 0 0 0-1.234-.008l-4.805 4.805-4.805-4.805a.89.89 0 0 0-1.234 0c-.336.344-.328.914 0 1.234l4.805 4.805-4.805 4.813a.88.88 0 0 0 0 1.226z"
                    fillOpacity=".8"
                    fill="#141029"
                    fillRule="nonzero"
                  />
                </g>
              </svg>}
            </div>
          </div>
        </div>
      </div>

      {isMobile && isMobileMenuOpen && <div className={`header__mobile ${isMobileMenuOpen ? 'active' : ''}`}>
        <div className="container">
          <div className="mobile__line"></div>
          <LinkExternal href={whitepaperUrl}><CommonButton className="mobile__btnwp">Whitepaper</CommonButton></LinkExternal>
          <div className="soc">
            {/*<LinkExternal href={contractUrl}>
              <img src="/assets/images/document.svg" alt="" title="" width="" height="" />
              <span>Contract</span>
            </LinkExternal>*/}
            <LinkExternal href={twitterUrl}>
              <img src="/assets/images/twitter.svg" alt="" title="" width="" height="" />
              <span>Twitter</span>
            </LinkExternal>
            {/*<LinkExternal href={telegramUrl}>
              <img src="/assets/images/telegram.svg" alt="" title="" width="" height="" />
              <span>Telegram</span>
            </LinkExternal>*/}
          </div>
          <CommonButton className="mobile__connect" variant="mod" onClick={connectWallet}>{accountShort}</CommonButton>
        </div>
      </div>}
    </header>
  );
};

export default hot(module)(AppHeader);
