import { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { hot } from 'react-hot-loader';

import { Container, Segment, Button, Header, Icon, Modal as ModalUI } from 'semantic-ui-react';

import Modal, { useModalState } from 'react-simple-modal-provider';
import { toast } from 'react-toastify';
import Number from 'react-number-format';

import './MintModal.scss';
import CommonButton from '../../common/CommonButton';

import { useEagerConnect, useActiveWeb3React } from '../../../hooks/index';


import { ethers, utils } from 'ethers';
import * as A from '../../../store/actions';
import * as S from '../../../store/selectors';


import getNftContract from '../../../hooks/getNftContract';
import getGameContract from '../../../hooks/getGameContract';
import getGameAddress from '../../../hooks/getGameAddress';


import { networkChainId, price2, price3, limit1, limit2 } from '../../../configs';


function getWlData(addr = '', list = []) {
  const address = addr.toLowerCase();
  let i = 0;
  while (i < list.length) {
    const address2 = list[i].address.toLowerCase();
    if (address === address2) {
      return [true, list[i]];
    }
    i++;
  }
  return [false, null];
}


const MintModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) document.body.classList.add("no-scroll");
    else document.body.classList.remove("no-scroll");
  }, [isOpen]);

  const dispatch = useDispatch();

  const triedEager = useEagerConnect();
  const context = useActiveWeb3React();
  const { library, chainId, account, active } = context;

  const [isActiveFreeMint, setIsActiveFreeMint] = useState(false);
  const [isActiveWhitelistMint, setIsActiveWhitelistMint] = useState(false);
  const [isActivePublicMint, setIsActivePublicMint] = useState(false);
  const [price, setPrice] = useState(0);

  // { address, amount, proof, }
  const [wl1, setWl1] = useState([]);
  const [isWl1, setIsWl1] = useState(false);
  const [wl2, setWl2] = useState([]);
  const [isWl2, setIsWl2] = useState(false);
  const [minted1, setMinted1] = useState(0);
  const [minted2, setMinted2] = useState(0);

  useEffect(() => {
    const [isWl1, wl1Data] = getWlData(account, wl1);
    if (isWl1) {
      toast('You are whitelisted for a free mint!', { autoClose: 5000, });
      setIsWl1(true);
    } else {
      setIsWl1(false);
    }

    const [isWl2, wl2Data] = getWlData(account, wl2);
    if (isWl2) {
      toast('You are whitelisted!', { autoClose: 5000, });
      setIsWl2(true);
    } else {
      setIsWl2(false);
    }
  }, [account]);

  useEffect(() => {
    if (isWl1 && minted1 >= 1)
      setIsWl1(false);
    if (isWl1 && minted2 >= 5)
      setIsWl2(false);
  }, [minted1, minted2]);


  const blockNumber = useSelector(S.provider.getBlockNumber);
  const [isUpdating, setUpdating] = useState(false);

  const [userBalanceEth, setUserBalanceEth] = useState(0.000000);
  const [userBalanceNft, setUserBalanceNft] = useState(0);
  const [nftTotalSupply, setNftTotalSupply] = useState(0);

  const onBlockHandler = async () => {
    if (isUpdating) return;
    setUpdating(true);
    //toast('onBlockHandler', { autoClose: 5000, });

    dispatch(A.provider.setBlockNumber(blockNumber));
    try {
      let userBalanceEth = await library.getBalance(account);
      userBalanceEth = utils.formatEther(userBalanceEth.toString());
      userBalanceEth = parseFloat(parseFloat(userBalanceEth).toFixed(4));
      setUserBalanceEth(userBalanceEth);

      const constractNft = getNftContract(chainId, library);
      if (constractNft) {
        const nftTotalSupply = await constractNft.totalSupply();
        setNftTotalSupply(nftTotalSupply.toNumber());

        const minted1 = await constractNft.minted1(account);
        setMinted1(minted1.toNumber());
        const minted2 = await constractNft.minted2(account);
        setMinted2(minted2.toNumber());

        const isActivePublicMint = await constractNft.isActivePublicMint();
        if (isActivePublicMint) {
          setIsActivePublicMint(true);
          setIsActiveWhitelistMint(false);
          setIsActiveFreeMint(false);
          setPrice(price3);
        } else {
          const isActiveWhitelistMint = await constractNft.isActiveWhitelistMint();
          const isActiveFreeMint = await constractNft.isActiveFreeMint();

          if (isActiveFreeMint && isWl1) {
            setIsActivePublicMint(false);
            setIsActiveWhitelistMint(false);
            setIsActiveFreeMint(true);
            setPrice(0);
          } else if (isActiveWhitelistMint) {
            setIsActivePublicMint(false);
            setIsActiveWhitelistMint(true);
            setIsActiveFreeMint(false);
            setPrice(price2);
          } else {
            setIsActivePublicMint(false);
            setIsActiveWhitelistMint(false);
            setIsActiveFreeMint(false);
            setPrice(price3);
          }
        }
      }

    } catch (error) {
      console.error(error);
      //dispatch(A.nft.clear());

      setIsActivePublicMint(false);
      setIsActiveWhitelistMint(false);
      setIsActiveFreeMint(false);
      setPrice(price3);
    }
    setUpdating(false);
  }

  useEffect(() => {
    onBlockHandler();
  }, [blockNumber]);

  useEffect(() => {
    fetch('/wl1.json').then(res => res.json()).then(json => setWl1(json)).catch(err => null);
    fetch('/wl2.json').then(res => res.json()).then(json => setWl2(json)).catch(err => null);
    onBlockHandler();
  }, []);



  const [maxLimit, setMaxLimit] = useState(0);
  useEffect(() => {
    let maxLimit = 4000 - nftTotalSupply;
    if (!isActivePublicMint && isActiveWhitelistMint && !isActiveFreeMint) maxLimit = (limit2 - minted2);
    if (!isActivePublicMint && !isActiveWhitelistMint && isActiveFreeMint) maxLimit = (limit1 - minted1);
    setMaxLimit(maxLimit);
  }, [isActivePublicMint, isActiveWhitelistMint, isActiveFreeMint, nftTotalSupply, minted1, minted2]);

  const [tokenCount, setTokenCount] = useState(0);
  const onChangeTokenCount = (e) => {
    e.preventDefault();
    /*if (e.target.value === '') {
      setTokenCount(0);
      return;
    }*/

    let count = parseInt(e.target.value);
    if (typeof count === 'number') {
      let maxLimit = 4000 - nftTotalSupply;
      if (!isActivePublicMint && isActiveWhitelistMint && !isActiveFreeMint) maxLimit = (limit2 - minted2);
      if (!isActivePublicMint && !isActiveWhitelistMint && isActiveFreeMint) maxLimit = (limit1 - minted1);

      if (count >= 0 && count <= maxLimit) {
        setTokenCount(count);
        return;
      }
    }
  }

  const onMint = async (e) => {
    e.preventDefault();

    if (tokenCount === 0 || nftTotalSupply >= 4000) {
      return;
    }

    if (chainId !== networkChainId) {
      toast.error('Wrong Network!', { autoClose: 5000, });
      return;
    }

    if (price * tokenCount >= userBalanceEth) {
      toast.error('Insufficient funds!', { autoClose: 5000, });
      return;
    }

    const constractNft = getNftContract(chainId, library, true);
    if (!constractNft) {
      toast.error('Error', { autoClose: 5000, });
      return;
    }
    try {

      let tx;
      let value = utils.parseEther((price * tokenCount).toString());
      let gasPrice = await library.getGasPrice();
      gasPrice = gasPrice.mul(120).div(100);
      let gasLimit;

      if (isActivePublicMint) {
        gasLimit = await constractNft.estimateGas.publicMint(
          tokenCount,
          { value, gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);
        tx = await constractNft.publicMint(
          tokenCount,
          { value, gasPrice, gasLimit, }
        );

        toast('Waiting for Transaction confirmation');
        await tx.wait(1);
        toast('Transaction confirmed');
        setTokenCount(0);
        return
      } else if (isActiveWhitelistMint && isWl2) {
        const [isWl2_, wl2Data] = getWlData(account, wl2);

        gasLimit = await constractNft.estimateGas.whitelistMint(
          account,
          tokenCount,
          wl2Data.proof,
          { value, gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);
        tx = await constractNft.whitelistMint(
          account,
          tokenCount,
          wl2Data.proof,
          { value, gasPrice, gasLimit, }
        );

        toast('Waiting for Transaction confirmation');
        await tx.wait(1);
        toast('Transaction confirmed');
        setTokenCount(0);
        return
      } else if (isActiveFreeMint && isWl1) {
        const [isWl1_, wl1Data] = getWlData(account, wl1);

        gasLimit = await constractNft.estimateGas.freeMint(
          account,
          wl1Data.proof,
          { value, gasPrice, }
        );
        gasLimit = gasLimit.mul(120).div(100);
        tx = await constractNft.freeMint(
          account,
          wl1Data.proof,
          { value, gasPrice, gasLimit, }
        );

        toast('Waiting for Transaction confirmation');
        await tx.wait(1);
        toast('Transaction confirmed');
        setTokenCount(0);
        return
      }

    } catch (error) {
      toast.error('Error!');
    }
  }

  return (
    <Modal
      id={'MintModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
      width={300}
      height={300}
      radius={10}
      backgroundColor='#212529'
    >
      <div className='mint-modal'
      //style={{ margin: '-120px', color: '#ff892f' }}
      >
        <Header as='h2' inverted
        //style={{ color: '#ff892f' }}
        >Minted <Number value={nftTotalSupply} thousandSeparator suffix='/4,000' displayType='text' /></Header>
        <br />
        <hr style={{ backgroundColor: 'red', height: '2px', }} />
        <br />
        <br />
        Balance: <Number value={userBalanceEth} suffix=' ETH' displayType='text' />
        <br />
        <ModalBody
          isActivePublicMint={isActivePublicMint} isActiveWhitelistMint={isActiveWhitelistMint} isActiveFreeMint={isActiveFreeMint}
          isWl1={isWl1} isWl2={isWl2}
          price={price}
          tokenCount={tokenCount} onChangeTokenCount={onChangeTokenCount}
          nftTotalSupply={nftTotalSupply}
          onMint={onMint}
          maxLimit={maxLimit}
        />
      </div>
    </Modal>
  );
};

export default hot(module)(MintModal);

function ModalBody({
  isActivePublicMint, isActiveWhitelistMint, isActiveFreeMint,
  isWl1, isWl2,
  price,
  tokenCount, onChangeTokenCount,
  nftTotalSupply,
  onMint,
  maxLimit,
}) {

  if (!isActivePublicMint && !isActiveWhitelistMint && !isActiveFreeMint)
    return (<b style={{ color: 'red', }}>Mint not started.</b>);

  if (
    !isActivePublicMint && isActiveWhitelistMint && !isWl2
  )
    return (<b style={{ color: 'red', }}>Not Whitelisted</b>);


  return (
    <>
      {price.toFixed(3)} ETH x <input
        id="tokenCount"
        name="tokenCount"
        type="number"
        min={0} step={1} max={4000 - nftTotalSupply}
        value={tokenCount}
        onChange={onChangeTokenCount}
        style={{
          backgroundColor: 'white', color: 'black',
          textAlign: 'center',
        }}
      /> = {(price * tokenCount).toFixed(3)} ETH
      <br />
      <i style={{ color: 'red', }}>max: <Number value={maxLimit} thousandSeparator displayType='text' /></i>

      <div style={{}}>
        <br />
        <br />
        <CommonButton onClick={onMint} style={{ width: '100%', }} >MINT</CommonButton>
      </div>
    </>
  );
}


function min(a, b) {
  if (a < b) return a;
  else return b;
}
