//import { useModalProps } from 'react-simple-modal-provider';
import { IconWrapper, IconClose, } from '..';
import './ModalBodyWrapper.scoped.scss';



export default function ModalBodyWrapper({ className, setOpen, children, silenceOutsideClick = false, }) {
  const computedModifiers = 'mask-dark mask-centered mask-blur';
  return (
    <div
      className={`mask ${computedModifiers} ${className ? className : ''}`}
      tabindex="0"
      onClick={() => (silenceOutsideClick ? null : setOpen(false))}
    >
      <div className="plate">
        <IconWrapper width="24" height="24" className="plate__icon" onClick={() => setOpen(false)}>
          <IconClose />
        </IconWrapper>
        {children}
      </div>
    </div>
  );
};
