import { hot } from 'react-hot-loader';
import './CommonButton.scoped.scss';



function CommonButton({ children, className, variant = 'base', size, onClick, style }) {
  return (
    <button
      className={`button button-${variant} ${size ? 'button-size-' + size : ''} ${className ? className : ''}`}
      onClick={onClick}
      style={style}
    >
      <span>{children}</span>
    </button>
  );
}

export default hot(module)(CommonButton);
