import { hot } from 'react-hot-loader';
import { Switch, Route, Redirect } from 'react-router-dom';
import { routerPaths } from './helper';
import { ErrorBoundary } from '../components';

import HomePage from '../pages/HomePage';

import TermsPage from '../pages/TermsPage';
import GamePage from '../pages/GamePage';
import PrivacyPage from '../pages/PrivacyPage';
import CookiePage from '../pages/CookiePage';



function AppRouter() {
  return (
    <ErrorBoundary>
      <Switch>
        <Route exact path={routerPaths.main} component={HomePage} />
        <Route exact path={routerPaths.terms} component={TermsPage} />
        <Route exact path={routerPaths.game} component={GamePage} />
        {/*
        <Route exact path={routerPaths.privacy} component={PrivacyPage} />
        <Route exact path={routerPaths.cookie} component={CookiePage} />*/}

        <Route path="*"><Redirect to={routerPaths.main} /></Route>
      </Switch>
    </ErrorBoundary>
  );
}

export default hot(module)(AppRouter);
