import { useEffect } from 'react';
import { hot } from 'react-hot-loader';

import { Container, Button, Header, Icon, Modal as ModalUI } from 'semantic-ui-react';

import Modal, { useModalState } from 'react-simple-modal-provider';
import { toast } from 'react-toastify';

import CommonButton from '../../../components/common/CommonButton';

import './WalletConnectModal.scss';

import useAuth from '../../../hooks/useAuth';


import { networkChainId } from '../../../configs';
async function switchEthereumChain() {
  try {
    if (networkChainId === 1) {
      return await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: '0x1', }],
      });
    } else if (networkChainId === 5) {
      return await window.ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: '0x5', }],
      });
    }
  } catch (switchError) {
    toast.error('Please switch MetaMask to the Ethereum Mainnet');
  }
}

const WalletConnectModal = ({ children }) => {
  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) document.body.classList.add("no-scroll");
    else document.body.classList.remove("no-scroll");
  }, [isOpen]);

  const { login } = useAuth();
  const connectorLocalStorageKey = 'connectorId';
  const onConnectToMetamask = () => {
    const connectorId = 'injected';
    login(connectorId);
    window.localStorage.setItem(connectorLocalStorageKey, connectorId);
    switchEthereumChain();
    setOpen(false);
  }
  const onConnectToWalletConnect = () => {
    const connectorId = 'walletconnect';
    login(connectorId);
    window.localStorage.setItem(connectorLocalStorageKey, connectorId);
    setOpen(false);
  }

  return (
    <Modal
      id={'WalletConnectModal'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      draggable={false}
      duration={300}
      width={300}
      height={300}
      radius={10}
      backgroundColor='#212529'
    >
      <Header inverted>Connect wallet</Header>
      <br />
      <div>
        <CommonButton onClick={onConnectToMetamask}
          style={{ width: '100%', marginBottom: '10px' }}
        >MetaMask</CommonButton>
      </div>
      <div>
        <CommonButton onClick={onConnectToWalletConnect}
          style={{ width: '100%', }}
        >Wallet Connect</CommonButton>
      </div>
    </Modal >
  );
};

export default hot(module)(WalletConnectModal);
