import { hot } from 'react-hot-loader';
import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';
import ScrollToTopOnMount from './ScrollToTopOnMount';
import { twitterUser } from '../../configs';



function AppSeo({ title, description, meta, children }) {
  const { i18n, t } = useTranslation();
  //const defaultTitle = `${t('ProjectName')} - ${t('ProjectSlogan')}`;
  const defaultTitle = `CASH RUSH is world's first Biggest Ethereum Compound Game With Gansta Appeal.`;
  const metaDescription = description || defaultTitle;

  return (
    <>
      <ScrollToTopOnMount />
      <Helmet
        htmlAttributes={{
          lang: i18n.language,
        }}

        title={title}
        defaultTitle={defaultTitle}
        titleTemplate={defaultTitle ? `%s | ${t('ProjectName')}` : undefined}

        meta={[
          {
            name: `description`,
            content: metaDescription,
          },
          {
            property: `og:title`,
            content: title,
          },
          {
            property: `og:description`,
            content: metaDescription,
          },
          {
            property: `og:type`,
            content: `website`,
          },
          {
            name: `twitter:card`,
            content: `summary_large_image`,
          },
          {
            name: `twitter:creator`,
            content: twitterUser ? twitterUser : '',
          },
          {
            name: `twitter:title`,
            content: title,
          },
          {
            name: `twitter:description`,
            content: metaDescription,
          },
        ].concat(meta)}
      />
      {children}
    </>
  )
}

export default hot(module)(AppSeo);
