import { useEffect } from 'react';
import { hot } from 'react-hot-loader';
import Modal, { useModalState } from 'react-simple-modal-provider';
import ModalBodyWrapper from '../ModalBodyWrapper';
import { trailerUrl } from '../../../configs';
import './PlatePlay.scoped.scss';



function PlatePlay({ children }) {
  const [isOpen, setOpen] = useModalState();
  useEffect(() => {
    if (isOpen) document.body.classList.add("no-scroll");
    else document.body.classList.remove("no-scroll");
  }, [isOpen]);

  return (
    <Modal
      id={'PlatePlay'}
      consumer={children}
      isOpen={isOpen}
      setOpen={setOpen}
      //asyncOpen={asyncOpen}
      draggable={false}
      duration={300}
    //width={300}
    //height={300}
    //radius={20}
    //backgroundColor={'#fff'}
    //animation={modalAnimation.slideUp} // scaleUp slideDown slideUp
    >
      <ModalBodyWrapper setOpen={setOpen} className="modal yt">
        <iframe
          width="100%"
          src={trailerUrl}
          title="YouTube video player"
          frameborder="0"
          height="600"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        />
      </ModalBodyWrapper>
    </Modal>
  );
};

export default hot(module)(PlatePlay);
